blockquote {
  font-size: 1.5625rem;
  font-family: $um-ro-font;
  line-height: 1.1;
  text-align: center;
  margin: 4rem 0;
  h6 {
    font-size: 1rem;
    font-weight: 800;
    margin-top: .75rem;
  }
}

figcaption {
  background-color: $um-gray-lt;
  font-size: .875rem;
  font-family: $um-ro-utility-font;
  font-weight: 700;
  margin-bottom: 1rem;
  padding: .625rem;
}