// =========
// This file is a custom file that loads all files. Each non-base layer
// can be disabled.
//
// Do not name this file "STARTER.scss". This file should be named
// THEMENAME.scss.
//
// This application file (THEMENAME.scss) is where all the partials are
// imported.
//
// Theme styles are categorized using SMACSS standards. They utilize
// categorization of styles into various categories. Those categories are the
// following:
//
// - Base: CSS reset/normalize plus HTML element styling.
// - Layout: Macro arrangement of a web page, including any grid systems.
// - Modules: Dictate minor layout modules or reusable elements.
// - States: Describe the appearance of a module in various states.
// - Theme: Specific customizations for your subtheme.
// For more information about this new Drupal css file standard, please review
// the following:
// - https://drupal.org/node/1887922
// - http://smacss.com/
// =========
@import url('https://fonts.googleapis.com/css?family=Raleway|Roboto|Roboto+Condensed');

// Make sure the charset is set appropriately
@charset 'utf-8';

//This is your _settings.scss file where you can override foundation variables.
@import 'settings';

// Core
// The following imports bootstrap the theme.
@import 'foundation';
@import 'motion-ui';

// Set flex to true to enable the new Foundation 6 flex grid.
$flex: true;
@include foundation-everything($flex);

@include motion-ui-transitions;
@include motion-ui-animations;


// Components

@import 'states/animate';

// Drupal specific styles (to fix Drupalisms).
@import 'base/drupal';

// Styling for elements.
// Optional.
@import 'base/elements';
@import 'base/global';

// Layout
// Each section of the document has it's own partial seperated out to improve
// developer experience. Additionally, grids can be declared in layouts as well.
@import 'layout/header';
@import 'layout/main';
@import 'layout/aside';
@import 'layout/footer';

// Modules
@import 'modules/accordion';
@import 'modules/block';
@import 'modules/button';
@import 'modules/card';
@import 'modules/menu_block';
@import 'modules/search';
@import 'modules/table';
@import 'modules/topbar';
@import 'modules/views';

// Themes
@import 'theme/page_header';
@import 'theme/index';

// Older IE support
// @import 'vendor/ie';
