$pg-header-blue: rgb(213, 225, 241);

.container-pg-header {
  background: $pg-header-blue;
  background-image: linear-gradient(0deg, rgba($pg-header-blue, 1) 0%, rgba($pg-header-blue, .8) 25%, rgba($pg-header-blue, 0) 100%), url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23fefeff' fill-opacity='.4' fill-rule='evenodd'%3E%3Ccircle cx='3' cy='3' r='3'/%3E%3Ccircle cx='13' cy='13' r='3'/%3E%3C/g%3E%3C/svg%3E");
  padding: 2rem 0;

  .grid-x {
    position: relative;
  }

  .section-title {
    color: $um-blue-md;
    display: inline-block;
    font: $font-bold .875rem $um-ro-font;
    left: 0;
    position: absolute;
    text-transform: uppercase;
    top: -6px;
  }

  .page-banner-title {
    color: $black;
    font-family: $um-font-pg-title;
    font-size: 1.875rem;
    line-height: 1;
    margin: 0;
  }

  // NEED HELP BLOCK
  .block-needhelp-2,
  .block-needhelp {
    @include breakpoint(small only) {
      margin-top: 1.5rem;
    }

    .block-title {
      color: $um-blue;
      font: $font-regular 1.5rem $um-font-pg-title;
      margin-bottom: .75rem;
      padding-left: 1em;
      text-transform: uppercase;
    }

    h4 {
      color: $um-blue-md;
      font: $font-semi-bold 1rem $um-ro-font;
      margin-bottom: 0;
      text-transform: uppercase;
    }

    p {
      color: $black;
      font: .875rem/1.4 $um-ro-utility-font;
      margin-bottom: 0;
    }

    .help-email {
      margin-top: .5rem;

      a {
        font: $font-semi-bold 1rem/1 $um-ro-font;
      }
    }

    .fa {
      color: $red;
      display: block;
      float: left;
      font-size: 1.125rem;
      margin-bottom: 3em;
      margin-right: .5em;
    }

    .fa-envelope {
      font-size: 1rem;
      margin-bottom: 0;

      + a {
        font-family: $um-ro-font;
        font-weight: $font-semi-bold;
      }
    }
  }
}
